import language from 'statics/data/language.json'
import timeZone from 'statics/data/timezones.json'

class LoadLocaleData {
	constructor(lng, timezone) {
		this.language = lng;
		this.timezone = timezone;
	}

	getData(key) {
		if (key) {
			return this[key];
		}
	}
}

window.loadLocaleData = new LoadLocaleData(language, timeZone);
